.accordion-item {
  border-bottom: 1px solid #ddd;
  margin: 40px auto;
}

.accordion-item-header {
  padding: 10px 15px;
  position: relative;
  color: #212121;
  cursor: pointer;
  font-family: 'Montserrat-regular';
  font-size: 14px;
  line-height: 17px;
}

.accordion-item-header::after {
  background-image: url("../img/svg/plus.svg");
  content: '';
  width: 20px;
  height: 20px;
  display: inline-block;
}

.accordion-item-header::after {
  background-image: url("../img/svg/plus.svg");
  content: '';
  width: 20px;
  height: 20px;
  display: inline-block;
  position: absolute;
  right: 10px;
}

.accordion-item.show .accordion-item-header::after {
  background-image: url("../img/svg/line.svg");
  content: '';
  width: 19px;
  height: 19px;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: 0 10px;
}

.accordion-item-content {
  background: #fff;
  transition: opacity .4s ease;
  visibility: hidden;
  height: 0;
  opacity: 0;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.01em;
  color: #212121;
}

.accordion-item.show .accordion-item-content {
  padding: 10px 15px;
  visibility: visible;
  height: auto;
  opacity: 1;
}

.accordion-item.show .accordion-item-header {
  color: #212121;
  font-family: 'Montserrat-bold';
  font-size: 14px;
  line-height: 17px;
  letter-spacing: -0.01em;
}

.accordion-item.show .accordion-item-header .accordion-item.show .accordion-item-header::after {
  background-image: url("../img/svg/line.svg");
  content: '';
  width: 20px;
  height: 20px;
  display: inline-block;
}

